import { NextResponse } from "next/server"
import { getAllCompanies } from "@/lib/companies/metadata"

/**
 * API route to get all companies
 * GET /api/companies
 */
export async function GET() {
  try {
    const companies = await getAllCompanies()
    return NextResponse.json(companies, { status: 200 })
  } catch (error) {
    console.error("Error getting companies:", error)
    
    // Provide more detailed error information
    const errorMessage = error instanceof Error 
      ? error.message 
      : "Failed to get companies"
    
    // Check if it's a MongoDB connection error
    const isConnectionError = errorMessage.includes("MongoServerSelectionError") || 
                              errorMessage.includes("MongoNetworkError") ||
                              errorMessage.includes("SSL")
    
    return NextResponse.json(
      { 
        error: isConnectionError 
          ? "Database connection error. Please check your MongoDB connection." 
          : "Failed to get companies",
        details: process.env.NODE_ENV === "development" ? errorMessage : undefined
      },
      { status: 500 }
    )
  }
}

